import { useState } from "react";
import { useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import DeviceDetailsList from "../../Blocks/Devices/DevicesBlock/DeviceDetails/DeviceDetailsList";
import NavbarBlock from "../../Blocks/NavbarBlock/NavbarBlock";
import './TestScreen.scss'

export default function TestScreen() {
    const [data, setData] = useState([])
    const navigate = useNavigate()

    let pathname = window.location.pathname
    let id = pathname.split('/')[2]
    // console.log(id, 'sss')

    const getTestsData = () => {
        let accesToken = localStorage.getItem('accesToken')
        let AuthStr = 'Bearer ' + accesToken
        const requestOptions = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': AuthStr
            }
        };
        fetch(`https://apiv1.zis.ru/tests/panel/${id}`, requestOptions)
            .then(response => response.json())
            .then(result => { console.log(result, 'data'); setData(result) })
    }


    useEffect(() => {

        let accesToken = localStorage.getItem('accesToken');
        if (!accesToken) {
            window.location.href = '/'
            return false;
        }
        getTestsData()
    }, [])

    let voltage_min = data.voltage_min == null ? '0' : data.voltage_min
    let voltage_max = data.voltage_max == null ? '0' : data.voltage_max
    let amperage_max = data.amperage_max == null ? '0' : data.amperage_max
    let amperage_min = data.amperage_min == null ? '0' : data.amperage_min
    let power_min = data.power_min == null ? '0' : data.power_min
    let power_max = data.power_max == null ? '0' : data.power_max

    return (
        <div className="test_container">
            <NavbarBlock />
            <div className="test_right_part" >
                <div className="test_header">
                    <div className="test_first">
                        <Link onClick={() => navigate(-1)}>
                            <button>
                                <img src='../images/Fill-35.svg' alt='pictures' style={{ transform: 'rotate(180deg)' }} />
                                <p>Back</p>
                            </button>
                        </Link>
                        <h3>Test 544</h3>
                    </div>
                </div>
                <div className="test_right_name">
                    <div className="test_list">
                        <div className="list_name">
                            <h3>Test Results</h3>
                            <DeviceDetailsList nameFirst={'Status:'} nameSecond={''} />
                            <DeviceDetailsList nameFirst={'Start:'} nameSecond={data.start_date ? data.start_date.split('T')[0] + ' ' + data.start_date.split('T')[1].split('.')[0] : null} />
                            <DeviceDetailsList nameFirst={'End:'} nameSecond={data.end_date ? data.end_date.split('T')[0] + ' ' + data.end_date.split('T')[1].split('.')[0] : null} />
                            <DeviceDetailsList nameFirst={'Undervoltage:'} nameSecond={data.undervoltage} />
                            <DeviceDetailsList nameFirst={'Overvoltage:'} nameSecond={''} />
                            <DeviceDetailsList nameFirst={'Power outages:'} nameSecond={data.power_outages == null ? '0' : data.power_outages} />
                            <DeviceDetailsList nameFirst={'Impulse Surges:'} nameSecond={data.impulse_surges == null ? '0' : data.impulse_surges} />
                            <DeviceDetailsList nameFirst={'Consuption:'} nameSecond={data.consumption == null ? '0' : data.consumption + ' Wh'} />
                            <DeviceDetailsList nameFirst={'Voltage:'} nameSecond={voltage_min + ' ' + '-' + ' ' + voltage_max + ' V'} />
                            <DeviceDetailsList nameFirst={'Amperage:'} nameSecond={amperage_min + ' ' + '-' + ' ' + amperage_max + ' A'} />
                            <DeviceDetailsList nameFirst={'Power:'} nameSecond={power_min + ' ' + '-' + ' ' + power_max} />
                        </div>
                        <div className="list_name">
                            <h3>Test Settings</h3>
                            <DeviceDetailsList nameFirst={'Load:'} nameSecond={data.power_on === true ? 'Enable' : 'Disable'} />
                            <DeviceDetailsList nameFirst={'Undervoltage Trigger:'} nameSecond={data.lower_voltage_trigger == null ? '0' : data.lower_voltage_trigger + ' ' + 'V'} />
                            <DeviceDetailsList nameFirst={'Undervoltage Delay:'} nameSecond={data.lower_voltage_delay == null ? '0' : data.lower_voltage_delay + ' ' + 'sec'} />
                            <DeviceDetailsList nameFirst={'Overvoltage Trigger:'} nameSecond={data.upper_voltage_trigger == null ? '0' : data.upper_voltage_trigger + ' ' + 'V'} />
                            <DeviceDetailsList nameFirst={'Overvoltage Delay:'} nameSecond={data.upper_voltage_delay == null ? '0' : data.upper_voltage_delay + ' ' + 'sec'} />
                            <DeviceDetailsList nameFirst={'Power restore Delay:'} nameSecond={data.power_restore_delay == null ? '0' : data.power_restore_delay} />
                            <DeviceDetailsList nameFirst={'Startup Delay:'} nameSecond={data.startup_delay == null ? '0' : data.startup_delay + ' ' + 'sec'} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

