import DeviseDataBoardList from './DeviceDataBoardList'
import './DevLists.scss'

export default function DevLists({ events }) {
    // console.log(events, 'sssssss')
    return (
        <div className='devs_lists'>
            <div className='devs_lists_header' >
                <p>Date </p>
                <p>EventCode</p>
                <p>Description </p>
            </div>
            <div>
                {
                    events?.map((value, index) => <DeviseDataBoardList key={index} secondText={value.event_code}  firstText={value.event_time.split('T')[0] + ' ' + value.event_time.split('T')[1].split('.')[0] } />)
                }

            </div>

        </div>
    )
}