import React from "react"
import './Popup.scss'

export default function Popup(props) {
    return (props.trigger ?
        (
            <div className="popup_container" >
                <div className="popup_inner" >
                    <h3>{props.title}</h3>
                    <button
                        onClick={() => props.setTrigger(false)}
                    >
                        Ok
                    </button>
                </div>
            </div>
        )
        :
        ''
    )
}