import React from "react"
import './Popup.scss'

export default function Popup(props) {
    return (props.trigger ?
        (
            <div className="popup_containerf" >
                <div className="popup_innerf" >
                    <h3>{props.title}</h3>
                    <div>
                        <button
                            className="first"
                            onClick={() => {
                                props.setTrigger(false)
                                props.deleteClient()
                            }}
                        >
                            Accept
                        </button>
                        <button
                            className="second"
                            onClick={() => props.setTrigger(false)}
                        >
                            Cancel
                        </button>
                    </div>
                </div>
            </div>
        )
        :
        ''
    )
}