import { useState, useEffect } from "react";
import NavbarBlock from "../../Blocks/NavbarBlock/NavbarBlock";
import './EditUser.scss'
import md5 from 'md5'
import Popup from "../Settings/Popup";
import Popups from '../Clients/ClientsDetails/Popup'


export default function EditUser({ token, onLogout }) {
    const pathName = window.location.pathname
    const [name, setName] = useState(pathName.split('/')[3])
    const [password, setPassword] = useState('********')
    const [login, setLogin] = useState(pathName.split('/')[4])
    const [isActive, setIsActive] = useState(pathName.split('/')[6] === 'Yes' ? true : false)
    const [isAdmin, setIsAdmin] = useState(pathName.split('/')[5] === 'Yes' ? true : false)
    let hash_password = login.toLowerCase() + password
    let hash_password_result = md5(hash_password)
    const [editUserOk, setEditUserOk] = useState(false)
    const [editUserDisable, setEditUserDisable] = useState(false)
    const [buttonPopup, setButtonPopup] = useState(false)

    console.log(hash_password_result)

    // console.log(isActive, 'active')
    console.log(isAdmin, 'fdsg')
    console.log(hash_password_result)

    const editUser = () => {
        let accesToken = localStorage.getItem('accesToken')
        let AuthStr = 'Bearer ' + accesToken
        let raw = JSON.stringify({
            password: hash_password_result,
            name: name,
            is_active: isActive,
            is_admin: isAdmin
        });
        const requestOptions = {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': AuthStr
            },
            body: raw,
            redirect: 'follow'
        };
        fetch(`https://apiv1.zis.ru/users/${pathName.split('/')[2]}`, requestOptions)
            .then(response => response.json())
            .then(result => {
                console.log(result, 'lllllsjdh')
                if (result.result === 'success') {
                    setEditUserOk(true)
                }
                else {
                    setEditUserDisable(true)
                }
            })
    }


    const deleteUser = () => {
        let accesToken = localStorage.getItem('accesToken')
        let AuthStr = 'Bearer ' + accesToken
        const requestOptions = {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': AuthStr
            },
            redirect: 'follow'
        };
        fetch(`https://apiv1.zis.ru/users/${pathName.split('/')[2]}`, requestOptions)
            .then(response => response.json())
            .then(result => {
                // console.log(result, 'fdfdfgadfga')
                if (result.result === 'success') {
                    window.location = '/admins'
                }
            })
    }

    useEffect(() => {
        let accesToken = localStorage.getItem('accesToken');
        if (!accesToken) {

            window.location.href = '/'
            return false;
        }
    }, [])



    return (
        <div className="addNew_admins"  >
            <Popup trigger={editUserOk} setTrigger={setEditUserOk} title={'All changes are saved successfully'}  ></Popup>
            <Popup trigger={editUserDisable} setTrigger={setEditUserDisable} title={'Something went wrong'}  ></Popup>
            <Popups trigger={buttonPopup} setTrigger={setButtonPopup} title={'Are you sure you want to delete your account?'} deleteClient={deleteUser} ></Popups>
            <NavbarBlock token={token} onLogout={onLogout} />
            <div className="container_addnew_rightPart">
                <h3>Edit User</h3>
                <div className="inputDiv">
                    <p>Name:</p>
                    <input type={''} value={name} onChange={e => setName(e.target.value)} />
                </div>
                <div className="inputDiv">
                    <p>Password:</p>
                    <input type={'password'} value={password} onChange={e => setPassword(e.target.value)} />
                </div>
                <div className="inputDiv">
                    <p>Is Admin:</p>
                    <label className="container">
                        <input type="checkbox"
                            defaultChecked={isAdmin === true ? 'checked' : ''}
                            onChange={(e) => {
                                e.target.checked ? setIsAdmin(true) : setIsAdmin(false)
                            }}
                        />
                        <span className="checkmark"></span>
                    </label>
                </div>
                <div className="inputDiv">
                    <p>Is Active:</p>
                    <label className="container">
                        <input type="checkbox"
                            defaultChecked={isActive === true ? 'checked' : ''}
                            onChange={(e) => {
                                e.target.checked ? setIsActive(true) : setIsActive(false)
                            }}
                        />
                        <span className="checkmark"></span>
                    </label>
                </div>
                <div className="buttons_div" >
                    <button
                        onClick={() => editUser()}
                    >
                        <p>Save</p>
                    </button>
                    <button
                        // onClick={() => deleteButton()}
                        onClick={() => setButtonPopup(true)}
                    >
                        <p>Delete</p>
                    </button>
                </div>

            </div>
        </div>
    )
}